<template>
  <div class="container">
<h1>Login</h1>
  <form @submit.prevent="login">
    <input type="email" placeholder="Email" class="form-control my-2" v-model="usuario.email"/>
    <input type="password" placeholder="Contraseña" class="form-control my-2" v-model="usuario.password"/>
    <b-button class="btn-block" type="submit">Acceder</b-button>
  </form>
    <div v-if="mensaje !== ''">
      <p>{{mensaje}}</p>
    </div>
  </div>
</template>

<script>

import {mapActions} from 'vuex';


export default {
  name: "Login",
  data() {
    return {
      usuario: {
        email: '',
        password: ''
      },
      mensaje:''
    }
  },
  methods: {
    ...mapActions(['guardarUsuario']),
    login(){
      this.axios.post('/auth/login', this.usuario)
        .then( res => {
          console.log(res.data)
          const token = res.data.token;
          this.guardarUsuario(token);
        })
      .catch( e => {
        this.mensaje = e.response.data.errors || e.response.data.msg
      })
    }
  }
};
</script>

